@import './src/styles/mixins';
@import './src/styles/variables';
.blockOne {
  padding: 50px 10px;

  .blockWrapper{
    @include autoCentrWidthBlock();
    @include flexItemCentr();
    justify-content: space-around;
    flex-direction: row;
    flex-wrap: wrap;

    .aboutMe{
      display: flex;
      flex-direction: column;
      width: 50%;
      background: $backgroundGradient;
      box-shadow: $boxShadow;
      border-radius: 15px;
      padding: 20px;
      .welcome{
        font-size: 24px;
        padding: 20px 0;
      }
      .title{
        font-size: 42px;
        font-weight: 500;
        span{
          color: #1f88de;
          font-size: 42px;
          padding-left: 15px;
          font-weight: 700;
          letter-spacing: 2px;
          opacity: 1;
        }
      }
      span{
        font-size: 42px;
        letter-spacing: 1px;
        opacity: 0.7;
      }

      .discriptions {
        span{
          display: block;
          padding: 5px;
          font-size: 20px;
        }
      }
    }

    .avatar {
      max-width: 360px;
      box-shadow: $boxShadow;
      border-radius: 15px;
      img{
        width: 360px;
        height: 422px;
      }
    }
  }
}